export function getPlaceholder(field: string, value: string, locale: string) {
    switch (field) {
        case 'era':
        case 'dayPeriod':
            return value;
        case 'year':
        case 'month':
        case 'day':
            return placeholders.getStringForLocale(field, locale);
        case 'hour':
            return 'hh';
        case 'minute':
            return 'mm';
        default:
            return '';
    }
}
